import { Cell, Grid, Icon, Text, TextInput, Theme, useStyles } from '../../lib'
import { IconMap } from '../../lib/components/elements/Icon/generated/Icons'

export const IconLibrary = () => {
  const { classes } = useStyles(createStyles)
  return (
    <div>
      <TextInput icon='zoomFilled' />
      <Grid wrap>
        {Object.keys(IconMap).map((key: any) => (
          <Cell style={classes.cell}>
            <div className={classes.iconBox}>
              <Icon icon={key} />
            </div>
            <Text style={{ wordBreak: 'break-word' }}>{key}</Text>
          </Cell>
        ))}
      </Grid>
    </div>
  )
}

const createStyles = (theme: Theme) => ({
  iconBox: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    background: theme.pallete.surface.background,
    padding: '1rem',
    height: 80,
    width: 80,
    margin: '0 auto 0.5rem auto',
  } as React.CSSProperties,
  cell: {
    flexBasis: 100,
    textAlign: 'center',
    fontSize: theme.typography.sizes.text,
  } as React.CSSProperties,
})
